@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
:root {
	--violet-500: #690fad;
	--violet-400: #bb89ec;
	--violet-300: #e7e2f8;
	--green-500: #688900;
	--green-400: #cfd700;
	--white: #ffffff;
	--dark: #1e2024;
}

body {
	font-family: 'Outfit', sans-serif;
	background: var(--dark);
	color: var(--white);
	padding: 0 1rem;
}

.container {
	max-width: 500px;
	min-width: 320px;
	margin: 30px auto;
	overflow: auto;
	min-height: 300px;
	padding: 16px;
	border-radius: 5px;
	background: var(--violet-500);
	box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.3);
}

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20px;
}

.headerTitle {
	color: var(--white);
}

.btn {
	display: inline-block;
	background: var(--green-400);
	color: #fff;
	border: none;
	padding: 10px 20px;
	margin: 5px;
	border-radius: 5px;
	cursor: pointer;
	text-decoration: none;
	font-size: 15px;
	font-family: inherit;
}

.btn:focus {
	outline: none;
}

.btn:active {
	transform: scale(0.98);
}

.btn-block {
	display: block;
	width: 100%;
}

ion-icon {
	font-size: 24px;
	color: var(--white);
	--ionicon-stroke-width: 64px;
}

.task {
	background: var(--violet-400);
	margin: 5px;
	padding: 10px 20px;
	cursor: pointer;
	display: flex;
	align-items: center;
}

.task-details {
	margin-left: 1rem;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	user-select: none;
}
.task .reminder {
	opacity: 0;
	transition: 0.3s;
}
.task .reminder.show {
	opacity: 1;
}

.task h3 {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.task p {
	opacity: 0.8;
}

.add-task-container {
	overflow: hidden;
	max-height: 0;
	transition: max-height 0.25s ease-out;
}

.add-task-container.show {
	max-height: 500px;
	transition: max-height 0.5s ease-in;
}

.add-form {
	margin-bottom: 40px;
	padding: 0 1rem;
}

.form-control {
	margin: 20px 0;
}

.form-control label {
	display: block;
}

.form-control input {
	width: 100%;
	height: 40px;
	margin: 5px;
	padding: 3px 7px;
	font-size: 17px;
	outline: none;
}

.form-control-check {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.form-control-check label {
	flex: 1;
}

.form-control-check input {
	flex: 2;
	height: 20px;
}

footer {
	margin-top: 30px;
	text-align: center;
}
